import { Helmet } from "react-helmet";
import { ReactSVG } from "react-svg";
import { useSearchParams } from "react-router-dom";
import MinimizeSVG from "assets/icons/minimize.svg";
import MaximizeSVG from "assets/icons/maximize.svg";
import SettingsSVG from "assets/icons/settings.svg";
import InfoSVG from "assets/icons/info.svg";
import UnrealSVG from "assets/icons/unreal.svg";
import ConnectionStrengthSVG from "assets/icons/connection-strength.svg";
import "./player.css";
import "./player-overrides.css";
import { useCallback, useEffect, useState } from "react";
import PlayerSettings from "./settings/PlayerSettings";
import PlayerStats from "./stats/PlayerStats";

const UrlParams = {
    LOCATION: "location",
};

type IProps = {
    animateLoading: () => void;
    scriptLoaded: boolean;
};

function Player({ animateLoading, scriptLoaded }: IProps) {
    const [searchParams, setSearchParams] = useSearchParams();
    const [showControls, setShowControls] = useState(false);
    const fullscreen = () => {
        window.fullscreen();
    };

    const onWindowLoad = useCallback(() => {
        window.load();
        animateLoading();
        window.addResponseEventListener("handle_responses", (response) => {
            const data = JSON.parse(response);
            if (data.location && searchParams.get(UrlParams.LOCATION) !== data.Location) {
                setSearchParams({ [UrlParams.LOCATION]: data.location });
            }
        });
    }, []);

    

    useEffect(() => {
        const onKeydownHandler = (e: KeyboardEvent) => {
            if (e.keyCode === 120) {
                setShowControls(true);
            }
        }

        document.addEventListener("keydown", onKeydownHandler);

        return () => {
            document.removeEventListener("keydown", onKeydownHandler);
        };
    }, []);

    /* useEffect(() => {
        console.log("did mount", scriptLoaded);

        window.addEventListener("load", onWindowLoad);

        return () => {
            window.removeEventListener("load", onWindowLoad);
        };
    }, []); */

    useEffect(() => {
        if (scriptLoaded) {
            onWindowLoad();
        }
    }, [scriptLoaded]);

    return (
        <div>
            <Helmet>
                <script defer src="/scripts/webRtcPlayer.js" type="text/javascript" />
            </Helmet>
            <div id="playerUI" className="noselect">
                <div id="player" />
                <div id="overlay">
                    <div style={{ display: showControls ? "block" : "none" }} id="controls">
                        <button
                            type="button"
                            className="tooltip"
                            onClick={fullscreen}
                            id="fullscreen-btn">
                            <ReactSVG src={MinimizeSVG} id="minimize" />
                            <ReactSVG src={MaximizeSVG} id="maximize" />
                            <span className="tooltiptext">Fullscreen</span>
                        </button>
                        <button type="button" className="tooltip" id="settingsBtn">
                            <ReactSVG src={SettingsSVG} />
                            <span className="tooltiptext">Settings</span>
                        </button>
                        <button type="button" className="tooltip" id="statsBtn">
                            <ReactSVG src={InfoSVG} />
                            <span className="tooltiptext">Information</span>
                        </button>
                    </div>
                    <div id="unrealengine">
                        <p />
                        <ReactSVG src={UnrealSVG} />
                    </div>
                    <div id="connection" className="tooltip">
                        <ReactSVG src={ConnectionStrengthSVG} renumerateIRIElements={false} />
                        <span className="tooltiptext" id="qualityText">
                            Not connected
                        </span>
                    </div>
                    <PlayerSettings />
                    <PlayerStats />
                </div>
            </div>
        </div>
    );
}

export default Player;
