import Player from "components/player/Player";
import { useRef } from "react";
import gsap from "gsap";
import useExternalScripts from "hooks/useExternalScripts";
import styles from "./App.module.css";

const getElementByIdAsync = (id: string) =>
    new Promise((resolve) => {
        const getElement = () => {
            const element = document.getElementById(id);
            if (element) {
                resolve(element);
            } else {
                requestAnimationFrame(getElement);
            }
        };
        getElement();
    });

function App() {
    const { scriptLoaded } = useExternalScripts({ url: "/scripts/app.js" });
    const logoRef = useRef<HTMLDivElement>(null);

    const load = async () => {
        const divElement = await getElementByIdAsync("videoPlayOverlay");
        console.log("player", divElement ? "found" : "not found");
        if (logoRef.current) {
            logoRef.current.style.opacity = "1";

            const iWidth = window.innerWidth;
            // const iHeight = window.innerHeight;

            const preloader = gsap.timeline({ paused: false });
            preloader.to("#loader", { width: iWidth / 3, duration: 0.5 }, "half");
            preloader.to("#loader", { width: iWidth, left: 0, duration: 0.4 });
            preloader.to("#playButton-image", {
                opacity: 1,
                duration: 0.1,
                onComplete: () => {
                    const root = document.documentElement;
                    root.style.setProperty("--btn-opacity", "1");
                    if (logoRef.current) {
                        logoRef.current.style.transition = "none";
                    }
                },
            });
            preloader.to("#logo", {
                opacity: 0,
                duration: 0.5,
                onComplete: () => {
                    document.getElementById("videoPlayOverlay")?.classList.add("ready");
                    if (logoRef.current) {
                        logoRef.current.style.display = "none";
                    }
                },
            });
            preloader.to("#videoPlayOverlay", { opacity: 1, duration: 0.1 });
            preloader.to("#loadContainer", {
                opacity: 0,
                pointerEvents: "none",
                duration: 0.6,
                delay: 0.4,
            });
        }
    };

    return (
        <div id={styles.app} className="App">
            <div className={styles.loadContainer} id="loadContainer">
                <div className={styles.logo} ref={logoRef} id="logo">
                    <img src="/logo.svg" alt="Proun Logo" />
                </div>
                <div className={styles.loader} id="loader" />
            </div>
            <Player animateLoading={load} scriptLoaded={scriptLoaded} />
        </div>
    );
}

export default App;
